
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  /* background-color: #CCE9E1; */
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

/*left column */
.column {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  max-width: 60%;
  background-color: #cce9e1;
  height: 100vh;
  border-top-right-radius: 60px;
  border-bottom-right-radius: 60px;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100vh;
}

.column-fill {
  flex-grow: 1; 
}

.text1 {
  font-size: 90px;
  font-weight: 900;
  font-family: 'Poppins', sans-serif;
  margin-top: 100px; 
  
}

.qr {
  /* size: 250px; */
  margin-left: 40px;
  max-width: 100%; 
    height: auto;
   margin-top: 50px;
}

/* right column */
.column1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  max-width: 40%;
  height: 100vh;
  /* margin-top: 120px; */
}

.text2 {
  font-size: 75px;
  font-weight: lighter;
  font-family: 'Poppins', sans-serif;
  margin-top: 100px;
  /* margin-bottom: 20px; */
}

/* input fields */
.inputs {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin-top: -80px;
}

.input {
  display: flex;
  align-items: center;
  margin-bottom: 35px;
  width: 88%; 
  background: #ffffff;
  border-radius: 50px;
  border-width: 2px;
  border-color: #000000;
  border-style: solid;
  position: relative;
}

.input input {
  flex: 1;
  height: 50px;
  width: 100%;
  background: transparent;
  border: none;
  border-radius: 50px;
  outline: none;
  color: #797979;
  font-size: 19px;
  font-family: 'Poppins', sans-serif;
  padding-left: 20px;
  padding-right: 50px; 
}

/* Sign In and Google button*/
.submit, .signgoogle {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff; 
  background: #000000; 
  border-radius: 30px;
  font-size: 19px;
  font-weight: 700;
  cursor: pointer;
  font-family: 'Poppins', sans-serif;
  height: 55px;
  width: 88%; 
  margin-bottom: 30px; 
}
.submit:hover{
  background-color:#323837
}
.signgoogle:hover{
  background-color:#e6edec
}
.signgoogle {
  background: #ffffff; 
  color: #000000; 
  border: 2px solid #000000; 
}

/* Google icon */
.google {
  margin-left: 10px;
}


/* eye icon */
.eye {
  position: absolute;
    right: 15px; 
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
}

.row1 {
  display: flex;
  flex-direction: row;
  /* justify-content: space-between; */
  /* align-items: center; */
  width: 80%;
}

/* Add this CSS for the Login button */
.login-button {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  background: #000000;
  border-radius: 30px;
  font-size: 19px;
  font-weight: 700;
  cursor: pointer;
  font-family: 'Poppins', sans-serif;
  height: 55px;
  width: 88%;
  margin-bottom: 30px;
}

/* Add hover effect for the Login button */
.login-button:hover {
  background-color: #323837;
}

.submit-container{
  display: flex;
  gap: 30px;
  margin: 60px auto;
  width: 88%;
}